
.popup-backdrop {
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(17, 17, 17, 0.4);
    top: 0;
    left: 0;
    z-index: 10000;
    pointer-events: none;
    opacity: 0;
    visibility: hidden;
    transition: opacity 250ms var(--time-function), visability 250ms var(--time-function);
}

.popup__body {
    overflow: hidden;
    min-width: 100%;
    min-height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: scale(0.5);
    transition: all 250ms var(--time-function);

}

.show-popup {
    overflow: hidden;
}
body.show-popup .popup-backdrop {
    opacity: 1;
    visibility: visible;
    pointer-events: initial;
    
}
body.show-popup .popup__body {
    
    transform: scale(1);
}
.popup__content {
    border: 2px solid var(--clr-text-primary);
    border-radius: 18px;

    display: flex;
    flex-direction: column;

    overflow-y: scroll;

    position: relative;

    background-color: var(--clr-back-ground-secondary);
    color: var(--clr-text-primary);

    padding: 40px 24px;

    max-width: 335px;
    
    
@media screen and (min-width: 768px) {
    max-width: 579px;
    

    padding: 40px;

    flex-direction: row;
    flex-wrap: wrap;
    overflow-y: visible;
}
}

.popup__book__info {
    @media screen and (min-width: 768px) {
           max-width: 279px;
           margin-left: 24px;
        }
}
.popup-close-btn {
width: 24px;
height: 24px;
border: none;
background-color: transparent;
position: absolute;
    top: 12px;
    right: 12px;
    @media screen and (min-width: 768px) {
        top: 14px;
        right: 16px;
        width: 28px;
        height: 28px;

        

    }
        

    &::before,
    &::after {
        content: '';
    width: 18px;
    height: 2px;
    position: absolute;
    background-color: var(--clr-text-primary);
border: 0.5px solid var(--clr-text-primary);
    border-radius: 10px;
    }

    &::before {
transform: rotate(45deg);
top: 12px;
left: 5px;
    }
     &::after {
transform: rotate(-45deg);
top: 12px;
right: 5px;
    }
        @media screen and (max-width: 767px) {
            &::before {
                width: 18px;
                top: 10px;
                left: 3.5px;
            }
    
            &::after {
                width: 18px;
                top: 10px;
                right: 2.5px;
            }
        }
}



.popup__image {
    overflow: hidden;
    margin-bottom: 14px;

        
       

    border-radius: 8px;
    margin-bottom: 14px;

    @media screen and (min-width: 768px) {
            max-width: 192px;
            max-height: 281px;
            margin-bottom: 40px;
        }
}
.popup__image > img {
    height: 100%;
    width: 100%;
    
    
}
.popup__book_name {
    font-size: 16px;
    font-weight: 700;
    line-height: 1.125;
    text-transform: uppercase;
    margin-bottom: 4px;
    @media screen and (min-width: 768px) {
            font-size: 24px;
            margin-bottom: 8px;

        }
}

.popup__book_author {
    color: var(--clr-text-secondary);
    font-size: 12px;
    line-height: 1.125;
    font-style: italic;
    margin-bottom: 18px;
    @media screen and (min-width: 768px) {
            font-size: 14px;
            margin-bottom: 20px;
        }
}

.book__descr {
    max-height: 70px;
    overflow: scroll;
    margin-bottom: 14px;
    font-size: 14px;
    line-height: 1.28;
    margin-bottom: 14px;
    @media screen and (min-width: 768px) {
            margin-bottom: 20px;
            max-height: 100%;
            overflow: auto;
        }
}

.popup__market__list {
    display: flex;
    gap: 20px;
    align-items: center;
    margin-bottom: 40px;

}

.popup__market__list__item {
    display: block;
}

.popup-btn {
    border: 2px solid var(--clr-text-modified);
    border-radius: 40px;

    padding: 14px 23px;
    margin-bottom: 8px;

    color: var(--clr-text-primary);
    text-transform: uppercase;
    font-weight: 700;
    line-height: 1.33;

    transition: color 250ms var(--time-function), background-color 250ms var(--time-function);
    
    @media screen and (min-width: 768px) {
            font-size: 18px;
padding: 20px 0px;
width: 100%;
        }
        &:hover,
        &:focus {
            color: var(--clr-text-secondary-light-theme);
            background-color: var(--clr-back-ground-primary-modified);

        }
}

.popup-add-text {
color: rgba(17, 17, 17, 0.5);
font-size: 10px;
line-height: 1.2;
text-align: center;
max-width: 242px;
margin: 0 auto;

@media screen and (min-width: 768px) {
    font-size: 12px;
       max-width: 324px;
    }

    
}
.popup-hidden {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    position: absolute;
}