*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

@media screen and (max-width: 767px) {
  .categories-list-section {
    padding-bottom: 40px;
  }
}

.categories-list-container {
  max-height: 228px;
  width: 335px;
  overflow: auto;
}

.simplebar-scrollbar:before {
  background-color: #e0e0e0 !important;
}

.categories-list {
  flex-direction: column;
  gap: 24px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .categories-list {
    gap: 32px;
  }
}

.categories-list-item {
  cursor: pointer;
}

@media screen and (min-width: 768px) {
  .categories-list-item {
    font-size: 18px;
    line-height: 1.333;
  }
}

.categories-list-text {
  font-size: 16px;
  line-height: 1.125;
  font-weight: var(--font-weight-regular);
  color: #1119;
  transition: var(--time-function);
}

.categories-list-text:hover, .categories-list-text:focus {
  color: var(--clr-text-modified);
}

.categories-list-text.active {
  font-size: 14px;
  font-weight: var(--font-weight-bold);
  text-transform: uppercase;
  color: var(--clr-text-modified);
}

@media screen and (min-width: 768px) {
  .categories-list-container {
    max-height: 472px;
    width: 339px;
  }
}

.test {
  width: 200px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.btn-to-down {
  z-index: 10;
  cursor: pointer;
  border: 2px solid var(--clr-text-modified);
  background-color: #0000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: all .2s ease-in-out;
  display: flex;
  position: fixed;
}

.btn-to-down:hover, .btn-to-down:focus {
  border: 2px solid #3c2b88;
  transition: all .2s ease-in-out;
}

.scroll-down-icon {
  fill: var(--clr-text-modified);
}

.scroll-down-icon:hover, .scroll-down-icon:focus {
  fill: #3c2b88;
}

@media screen and (max-width: 767px) {
  .btn-to-down {
    width: 44px;
    height: 44px;
    top: 11%;
    right: 3%;
  }

  .scroll-down-icon {
    width: 18px;
    height: 18px;
  }
}

@media (min-width: 768px) {
  .btn-to-down {
    width: 54px;
    height: 54px;
    top: 15%;
    right: 2%;
  }

  .scroll-down-icon {
    width: 30px;
    height: 30px;
  }
}

@media (min-width: 1158px) {
  .btn-to-down {
    width: 64px;
    height: 64px;
    top: 15%;
    right: 3%;
  }

  .scroll-down-icon {
    width: 44px;
    height: 44px;
  }
}

.btn-to-down--visible {
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
  display: none;
}

