}

.footer-modal {
  width: 100%;
  height: 100vh;
  z-index: 9999;
  visibility: hidden;
  opacity: 0;
  background-color: #2e2f4266;
  justify-content: center;
  align-items: center;
  transition: opacity .4s, visibility .4s;
  display: grid;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
}

.footer-modal-box {
  max-width: 1400px;
  z-index: 1;
  background-color: #efefe3;
  margin: 50px;
  padding: 50px 15px;
  transition: transform .8s;
  position: relative;
  transform: scale(0);
}

.footer-modal.footer-open {
  visibility: visible;
  opacity: 1;
}

.footer-modal.footer-open .footer-modal-box {
  transform: scale(1);
}

.team-logo {
  color: #4f2ee8;
  fill: #4f2ee8;
  cursor: pointer;
  text-align: center;
  margin-top: 5px;
  margin-bottom: 5px;
  font-size: 18px;
  animation-name: team-logo;
  animation-duration: 3s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  display: block;
}

@keyframes team-logo {
  0% {
    color: #e82e2e;
    fill: #e82e2e;
  }

  25% {
    color: #e8e52e;
    fill: #e8e52e;
  }

  50% {
    color: #2ee82e;
    fill: #2ee82e;
  }

  75% {
    color: #2ee8df;
    fill: #2ee8df;
  }

  100% {
    color: #e82ec9;
    fill: #e82ec9;
  }
}

