input[type="checkbox"] {
  height: 0;
  width: 0;
  visibility: hidden;
}

label {
  cursor: pointer;
  text-indent: -9999px;
  width: 40px;
  height: 20px;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 100px;
  display: block;
  position: relative;
}

label:after {
  content: "";
  width: 19px;
  height: 18px;
  background: #fff;
  border-radius: 90px;
  transition: all .4s;
  position: absolute;
  top: 1px;
  left: 1px;
}

input:checked + label {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
}

input:checked + label:after {
  left: calc(100% - 1px);
  transform: translateX(-100%);
}

label:active:after {
  width: 40px;
}

.menu__toggle {
  height: 28px;
